@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

header.headerNav {
    position: fixed;
    width: 100vw;
    z-index: 10;
    border-bottom: 3px solid rgba(79, 79, 79, 0.65);
    backdrop-filter: blur(20px);

}
header.headerNav div.navContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 80px;
    max-width: 1124px;
    margin: auto;
    padding: 0 16px;

}

header.headerNav div.navContainer .logo {
    display: flex;
    align-items: center;
}

header.headerNav div.navContainer .logo span.textLogo {
    color: #fff;
    font-family: 'Poppins';
    font-weight: 700;
    font-size: 20px;
    position: relative;
    left: -10px;
}

#navbar {
    display: flex;
    align-items: center;
    gap: 48px;
    list-style: none;

}


#navbar li a{
    text-decoration: none;
    color: #fff;
    font-family: 'Poppins';
    font-weight: 700;
    position: relative;
    display: flex;
    align-items: flex-end;
    filter: drop-shadow(rgba(0, 0, 0, 0.16) 0px 1px 1px);

    
}

#navbar li a:hover,
#navbar li a.active {
    background: linear-gradient(90deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: Poppins;
    font-weight: 700;
}

#navbar li a:hover::after,
#navbar li a.active::after {
    content: " ";
    background: linear-gradient(90deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    position: absolute;
    height: 3px;
    width: 100%;
    left: 2px;
    bottom: -4px;
}



.menuIconContent {
    display: none;
}

@media screen and (max-width: 1023px) {
    #navbar {
        display: none;
    }
    nav.navbarView {
        overflow-y: hidden;
    }
    div.menuIconContent {
        display: block ;
        
    }
    .menuIcon {
        height: 32px;
        width: 32px;
    }
    #navbar {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        justify-content: flex-start;
        position: fixed;
        top: 85px;
        right: -300px;
        width: 300px;
        height: 100vh;
        background-color: #191a25;
        backdrop-filter: blur(20px);
        padding: 20px 0px 0px 50px;
        transition: 0.3s ease-in-out;
    }
    #navbar.active {
        right: 0;
    }
}
