section.profileContainer {
    position: relative;
    padding: 0px 30px;
}

div.profileBox {
    border-radius: 24px;
    max-width: 1300px;
    margin: auto;
    padding: 88px 94px 0 94px;
    background: rgba(96, 96, 96, 0.19); 
    backdrop-filter: blur(10px);
    border: 2px solid #544A4A;
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 30px
}

div.aboutMeContent {
    max-width: 591px;

}
div.aboutMeContent div.profileTitle {
    width: 100%;
    
}

div.aboutMeContent span.profileTitleText {
    font-family: Poppins;
    font-size: 55px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-left: 30px;
}

 div.profileTitle {
    max-width: 591px;
    

}
div.aboutMeContent div.descriptionContent {
    max-width:600px;
}


div.aboutMeContent div.descriptionContent span.descriptionText {
    color: #B3B3B3;
    
    font-family: 'Poppins';
    font-size: 16px;
    font-weight: 700;
}

div.profileBox div.profileDetails {
    background: linear-gradient(67deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    padding: 43px;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    gap: 10px;
}
div.profileBox div.profileDetails div {
    display: flex ;
    align-items: flex-start ;
    
}

div.profileBox div.profileDetails div span {
    color: #FFF;
    
    font-family: Poppins;
    font-size: 18px;
    font-style: normal;
    font-weight: 700;

}

div.profileBox div.profileDetails div img.iconProfileDetails {
    width: 26px;
    height: 26px;
    margin-right: 12px;
}
@media screen and (max-width:1058px) {
    div.profileBox {
        flex-direction: column;
    }

}
@media screen and (max-width:641px) {
    div.aboutMeContent span.profileTitleText {
        font-size: 30px;
        margin-left: 20px;


    }
    div.aboutMeContent div.profileTitle img {
        width: 29px;
        height: 29px;
    }
    div.aboutMeContent div.profileTitle {
        display: flex;
        align-items: center;
        margin-left: 20px;
    }
    div.aboutMeContent div.descriptionContent span.descriptionText {
        font-size: 14px;
        text-align: justify;
        text-indent: 80px;
    }
    div.aboutMeContent div.descriptionContent img {
        width: 79px;
        height: 79px;
        position: relative;
        top: 30px

    }
    div.profileBox {
        position: relative;
        padding: 40PX;
    }
    div.aboutMeContent div.descriptionContent  {
       display: flex;
       flex-direction: column;
        position: relative;
        top: -20px;
      
    }
    div.profileBox div.profileDetails {
        padding: 30px;
    }
    div.profileBox div.profileDetails div span {
        font-size: 12px;
    }


}
@media screen and (max-width:401px) {
    div.aboutMeContent span.profileTitleText {
        font-size: 20px;
    }
    div.aboutMeContent div.profileTitle img {
        width: 20px;
        height: 20px;
    }
    div.aboutMeContent div.descriptionContent img {
        height: 60px;
        width: 60px;
    }
    div.aboutMeContent div.descriptionContent span.descriptionText {
        text-indent: 60px;
        font-size: 12px;
    }
    div.profileBox div.profileDetails {
        padding: 20px
    }
    div.profileBox div.profileDetails div img.iconProfileDetails {
        font-size: 12px;
        height: 20px;
        width: 20px;
    }



}