@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');


* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  

}
section.body {
  width: 100%;
  background: #091121;

}

div.circleYellow {
  width: 326px;
  height: 326px;
  border-radius: 326px;
  background: rgba(147, 141, 0, 0.50);
  filter: blur(200px);
  position: absolute;
  top: -10rem;
  left: -10rem;
}
div.circleCyan {
  width: 326px;
  height: 326px;
  border-radius: 326px;
  background: rgba(118, 255, 173, 0.50);
  filter: blur(200px);
  position: absolute;
  top: 35rem;
  right: 0;
}

img.bannerImg {
  height: 600px;
}
