section.contentPresentation {
    padding: 0 36px;
    /* padding-top: 100px; */
    height: 760px;
    margin: auto;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
}

section.contentPresentation div.boxPresentation {
    padding: 62px 12px;
    max-width: 656px;
    height: fit-content;
    border-radius: 24px;
    grid-column: 2;
    position: relative;
    z-index: 2;
    background: rgba(96, 96, 96, 0.19); 
    backdrop-filter: blur(10px);
    border: 2px solid #544A4A;

}

section.contentPresentation div.boxPresentation div.contentDescription {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
}

h2.nameSpan {
    font-size: 40px;
    font-family: Poppins;
    font-weight: 700;
    color: #fff;
}

div .nameSpanContainer {
    display: flex;
}
h2.nameColor {
    padding-left: 15px ;
    font-size:  40px;
    font-family: Poppins;
    font-weight: 700;
    background: linear-gradient(88deg, #FFF500 0%, rgba(118, 255, 173, 0.81) 72.95%, #508DE9 99.34%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}


div.description {
    margin-top: 20px;
    margin-bottom: 4rem ;
    width: 70%;
}

div.description span {
    display: flex;
    color: #7B7B7B;
    font-family: Poppins;
    font-size: 20px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    text-align: center;
   

}

.footerBox {
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    padding: 0px 30px;
}

button.button {
    padding: 14px 18px;
 
 }
 button.button.cv {
     margin: 5px;
     background-color: #091121;
     border-radius: 5px;
     border: none;
    width: 100%;

 
 }
 div.buttonCvContainer {
     display: flex;
     background: linear-gradient(85deg, rgba(255, 245, 0, 0.70) 0.24%, rgba(118, 255, 173, 0.57) 54.7%, rgba(80, 141, 233, 0.70) 97.96%);
     align-items: center;
     justify-content: center;
     border-radius: 10px;
 
 }
 
 button.primary {
     padding: 14px 18px;
     border-radius: 10px;
     background: linear-gradient(85deg, rgba(255, 245, 0, 0.70) 0.24%, rgba(118, 255, 173, 0.57) 54.7%, rgba(80, 141, 233, 0.70) 97.96%);
     border: none;
 }
 
 button.primary span {
     color: #161616;
     font-family: Poppins;
     font-size: 22px;
     font-weight: 700;
 }
 
 button.button span {
     font-family: Poppins;
     font-size: 22px;
     font-style: normal;
     font-weight: 700;
     background: linear-gradient(88deg, #F2B705 0%, rgba(118, 255, 173, 0.81) 54.33%, #508DE9 99.34%);
     background-clip: text;
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
 }
 

img.bannerImg {
    height: 600px;
  }

  
@media screen and (max-width:1200px) {
    img.bannerImg {
        display: none;
    }
}
@media screen and (max-width:770px) {
    section .container {
        padding: 0px 30px;

    }

    h2.nameSpan, h2.nameColor {
        font-size: 30px;

    }
    

    div .box {
        width: 100%;
        padding: 40px 0px;
    }

    div.description span {
        font-size: 18px;

    }

    div.description {
        width: 80%;
        margin-bottom: 3rem ;
    }

    button.button span {
        font-size: 18px;
    }
    button.primary span {
        font-size: 18px;

    }
   
}

@media screen and (max-width:526px) {
    h2.nameSpan, h2.nameColor{
        font-size: 20px;

    }
    div.description span {
        font-size: 12px;

    }
  
    div.footerBox {
        padding: 0px 10px
    }
    button.button span, button.primary span  {
        font-size: 10px;
    }
    
    
}
  
